<template>
    <div class="pay-record">
		<div class="top-head mainBgColor" >

			<div class="main-item">
				<div class="info">
					<p>{{ $L('可用的余额') }}</p>
					<p  class="font-large">{{ balance }}</p>
				</div>
			</div>
			<div class="right-btn">
				<router-link :to="{ 'name':'payCharge'}">
					<el-button class="first-btn mainFontColor" >{{ $L('立即充值') }}</el-button>
				</router-link>
				<el-button @click="goHome" class="btn mainFontColor" >{{ $L('马上购物') }}</el-button>
			</div>
		</div>
		<div class="content-middle">
			<el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
			<el-select class="search" v-model="messageType" size="small"
					   @change="changeTypeFunc">
				<el-option
						v-for="item in messageTypelist"
						:key="item.type"
						:label="item.name"
						:value="item.type">
				</el-option>
			</el-select>
		</div>
		<div class="payrecord-list" v-if="tableData!== null && tableData.allNum>0" v-loading="loading">
			<div class="list-item" v-for="(item,index) in tableData.list" :key="index">
				<template v-if="item.orderType == 1">
					<div class="name"> {{ item.goodsName }}</div>
				</template>
				<template v-else-if="item.orderType == 2">
					<div class="name"> {{ $L('退款订单') }}</div>
				</template>
				<template v-else-if="item.orderType == 3">
					<div class="name"> {{ $L('录款')}}</div>
				</template>
				<template v-else-if="item.orderType == 4">
					<div class="name"> {{ $L('管理员扣款')}}</div>
				</template>
				<template v-else-if="item.orderType == 5">
					<div class="name"> {{ $L('在线充值')}}</div>
				</template>
				<template v-else>
					<div class="name"> {{ item.goodsName }}</div>
				</template>
				<div class="item">
					<template v-if="item.orderType == 1">
						<div class="left">
							{{ $L('订单号') }}:{{ item.orderSn }}
						</div>
					</template>
					<template v-else-if="item.orderType == 2">
						<div class="left">
							{{ $L('订单号') }}:{{ $L('自动退款没有订单号') }}
						</div>
					</template>
					<template v-else-if="item.orderType == 3">
						<div class="left">
							{{ $L('订单号') }}:{{ $L('管理员手动录款没有订单号') }}
						</div>
					</template>
					<template v-else-if="item.orderType == 4">
						<div class="left">
							{{ $L('订单号') }}:{{ $L('管理员手动扣款没有订单号') }}
						</div>
					</template>
					<template v-else-if="item.orderType == 5">
						<div class="left">
							{{ $L('订单号') }}:{{ $L('在线充值金额不生成订单号') }}
						</div>
					</template>
					<template v-else>
						<div class="left">
							{{ $L('订单号') }}:{{ $L('自动退款没有订单号') }}
						</div>
					</template>
					<div class="right">
<!--						{{ item.detailed }}-->
						{{ detailStatue(item.payType).text }}
					</div>
				</div>
				<div class="item success">
					<div class="right">
						<span :class="statusFunc(item.status).color">{{ statusFunc(item.status).text }}</span>
					</div>
				</div>
				<div class="item success">
					<div class="left time">
						{{ item.addTime }}
					</div>
					<div class="right font-green">
						{{ item.amount }}
					</div>
				</div>
			</div>
			<div class="list-pages">
				<el-pagination
						small
						background
						@size-change="handleSizeChange"
						@current-change="handleCurrentChange"
						layout="prev, pager, next"
						:total="tableData.allNum">
				</el-pagination>
			</div>

		</div>
		<div class="no-data" v-else>{{$L('暂无数据')}}</div>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
					<inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
									:weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
									@change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
    export default {
        name: "payRecord",
		data(){
			return{
				loading: true,
				errorType: 'error',
				errorTitle: this.$L('出错了'),
				errorTips: null,
				errorShow: false,
				balance:'',
				allIncome:'',
				tableData:{},
				pageSize:10,
				currentPage:1,
				messageType:0,
				msgArr:[],
				topMenu :[
					{
						'groupTitle':'',
						'menuList':[
							{text: this.$L('信息发布')}
						]
					}
				],
				messageTypelist:[
					{
						type:0,
						name:this.$L('全部状态')
					},
					{
						type:1,
						name:this.$L('已确认')
					},
					{
						type:2,
						name:this.$L('未确认')
					},
				],
				SearchTimeZone:"",
				type:0,
				dtCreateStart:'',
				dtCreateEnd:'',
				showTime:false,
				timeArr:[],
				defaultDate:[]
			}
		},
		created () {
			this.getData();
		},
		methods : {
			detailStatue(value){
				let x;
				switch(value){
					case 1:
						x = {text:this.$L('管理员录款')};
						break;
					case 2:
						x = {text:this.$L('管理员扣款')};
						break;
					case 3:
						x = {text:this.$L('支付宝支付')};
						break;
					case 4:
						x = {text:this.$L('快钱支付')};
						break;
					case 5:
						x = {text:this.$L('财付通支付')};
						break;
					case 6:
						x = {text:this.$L('银行支付')};
						break;
					case 7:
						x = {text:this.$L('货到付款')};
						break;
					case 8:
						x = {text:this.$L('环球支付')};
						break;
					case 9:
						x = {text:this.$L('贝宝支付')};
						break;
					case 10:
						x = {text:this.$L('邮局汇款')};
						break;
					case 11:
						x = {text:this.$L('微信支付')};
						break;
					case 12:
						x = {text:this.$L('微信手机支付')};
						break;
					case 13:
						x = {text:this.$L('网银在线')};
						break;
					case 14:
						x = {text:this.$L('退货退款')};
						break;
					case 15:
						x = {text:this.$L('余额支付')};
						break;
					default:
						x = {text:this.$L('其他')};
						break;
				}
				return x;
			},
			showError(title, tips, type) {
				if (type === 'warning') {
					this.errorType = 'warning';
				} else {
					this.errorType = 'error';
				}
				if (title && title !== '') {
					this.errorTitle = title;
				} else {
					this.errorTitle = this.$L('出错了');
				}
				if (tips && tips !== '') {
					this.errorTips = tips;
				} else {
					this.errorTips = null;
				}
				this.loading = false;
				this.errorShow = true;
			},
			getData() {
				const _self = this;
				_self.loading = true;
				let arr = {
					p : _self.currentPage,
					num : _self.pageSize,
					type : _self.type,
					dtCreateStart : _self.dtCreateStart,
					dtCreateEnd : _self.dtCreateEnd
				};
				_self.$api('getAccountList',arr , function (reData) {
					if (reData.flag === 1) {
						_self.tableData = reData.data;
						_self.balance = reData.data.balance;
						_self.allIncome = reData.data.allIncome;

						_self.loading = false;
					} else if (reData.flag === -1) {
						//登录超时了
						_self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
							confirmButtonText: _self.$L('重新登录'),
							cancelButtonText: _self.$L('取消'),
							type: 'error',
							distinguishCancelAndClose: true
						}).then(() => {
							_self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
						}).catch(action => {
							_self.$gotoWebHome();
						});
					} else {
						//未知错误
						_self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
							confirmButtonText: _self.$L('点击重试'),
							cancelButtonText: _self.$L('返回首页'),
							type: 'error',
							distinguishCancelAndClose: true
						}).then(() => {
							_self.getData();
						}).catch(action => {
							_self.$router.push({name: 'home'});
						});
					}
				}, function (error) {
					//网络请求错误
					_self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
						confirmButtonText: _self.$L('点击重试'),
						cancelButtonText: _self.$L('返回首页'),
						type: 'error',
						distinguishCancelAndClose: true
					}).then(() => {
						_self.getData();
					}).catch(action => {
						_self.$router.push({name: 'home'});
					});
				});
			},
			handleCurrentChange(val){
				this.currentPage = val;
				this.getData();
			},
			handleSizeChange(val){
				this.pageSize = val;
				this.getData();
			},
			statusFunc(value){
				let x;
				switch(value){
					case 0:
						x = {text:this.$L('未确认'),color:'font-red'};
						break;
					default:
						x = {text:this.$L('已确认'),color:'font-green'};
						break;
				}
				return x;
			},
			changeTypeFunc(val){
				this.type = val;
				this.getData();
			},
			SearchTimeFunc(time){
				console.log(time);
				if(time !== null){
					this.dtCreateStart = time[0];
					this.dtCreateEnd = time[1];
				}else{
					this.dtCreateStart = '';
					this.dtCreateEnd = '';
				}
				this.getData();
			},
			goHome(){
				this.$gotoWebHome();
			},
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},
		}
    }
</script>

<style lang="scss" scoped>
	@import "../../../assets/css/common/common";
	.pay-record{
		.top-head{
			padding: 20px;
			background: #fff;
			.main-item{
				display: block;
				text-align: center;
				color: #fff;
				i{
					font-size: 50px;
					vertical-align: super;
					margin-right: 10%;
				}
				.info{
					display: inline-block;
					p{
						&.font-large{
							font-size: 26px;
						}
						&:first-child{
							font-size: 16px;
						}
					}
				}

			}
			.right-btn{
				display: block;
				vertical-align: top;
				margin-top: 20px;
				text-align: center;
				a{
					button{
						background: #fff;
					}

				}
				.btn{
					margin-left: 30px;
				}
			}
		}
		.content-middle{
			height: 50px;
			background: #fff;
			line-height: 50px;
			margin: 10px 0;
			padding: 0 10px;
			.time-search{
				vertical-align: bottom;
			}
			.font{
				padding-left: 20px;
				font-size: 16px;
			}
			.search{
				width: 120px;
				padding-left: 10px;
			}
			.input-with-select{
				width: calc(100% - 480px);
				padding-left: 10px;
			}
		}
		.payrecord-list{
			position: relative;
			width:100%;
			height: auto;
			padding: 10px;
			background: #fff;
			.list-item{
				width: 100%;
				border-bottom: 1px solid #eee;
				.name{
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					font-size: 14px;
					width: 70%;
				}
				.item{
					height: 24px;
					line-height: 24px;
					.left{
						height: inherit;
						line-height: inherit;
						float: left;
						color: #ccc;
						display: inline-block;
						width: 70%;
					}
					.right{
						height: inherit;
						line-height: inherit;
						text-align: right;
						float: right;
						color: #333;
						display: inline-block;
						width: 30%;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}
				}
				.success{
					.font-red{
						color: #FF0000;
					}
					.font-green{
						color: #00bf00;
					}
					.time{
						color: #666;
					}
				}
			}
			.list-pages{
				text-align: center;
				padding: 40px 0 20px;
				background: #fff;
			}
		}
		.no-data{
			position: relative;
			width: 100%;
			height: 170px;
			line-height: 170px;
			text-align: center;
			color: #909399;
			background-color: #ffffff;
		}
		.error {
			position: absolute;
			z-index: 200;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #FFFFFF;
			overflow: hidden;

			.error-tips {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 320px;
				transform: translate(-50%, -50%);
			}
		}
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
	}
</style>
<style scoped>
	.el-message-box {
		width: 94% !important;
	}
</style>